import MapComponent from "../components/MapComponent";
import drinkImage from "../static/image/picture/drink.PNG";

export default function LocationContainer() {
    return (
        <div className="locationContainer">
            <MapComponent/>
            <div className="locationDetailContainer">
                <table style={{textAlign: 'left'}}>
                    <tr>
                        <td className="locationTitle">자가용</td>
                        <td>한국도심공항 터미널 주차장 이용</td>
                    </tr>
                    <tr>
                        <td className="locationTitle">지하철</td>
                        <td><span className="line2">2</span>삼성역 5번 출구 도보 약 7분<br/>
                            <div className="locationDetail">* 지하 코엑스몰 이동보다는 <span className="notoSansMedium">지상으로</span> 찾아오시면
                                편리합니다.<br/></div>
                            <span className="line9">9</span> 봉은사역 7번 출구 도보 약 10분
                        </td>
                    </tr>
                    <tr>
                        <td className="locationTitle">버스</td>
                        <td>한국무역센터삼성역 정류장<br/>
                            (정류장 코드 23-201)<br/>
                            <span className="notoSansMedium">간선</span> <span
                                className="fontNumLight">146, 333, 341, 360, 740</span><br/>
                            <span className="notoSansMedium">지선</span><span
                                className="fontNumLight"> 3411</span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; <span
                                className="notoSansMedium">광역</span> <span className="fontNumLight">M6450</span><br/>
                            <span className="notoSansMedium">직행</span> <span className="fontNumLight">1100, 1700, 2000, 2000-1, 7007,
                                500-2, 9303</span>
                        </td>
                    </tr>
                </table>

                <div className="guideDiv">
                    <img src={drinkImage} style={{width:'45%'}}></img>
                    <div className="bold">
                        예식 1시간 전부터<br/>
                        웰컴드링크와 포토부스가 마련됩니다.
                    </div>
                    촬영은 무료 및 무제한이며, <br/>
                    좋은 날 추억을 기록으로 남길 수 있도록<br/>
                    많은 참여 부탁드립니다.
                </div>
            </div>
        </div>
    );
}